body{
  background-color: #f6f6f6!important;
  .menu{
    .container{
      .navbar{
        padding: 0.5rem 0;
        .navbar-brand{
          img{

          }
        }
        .collapse{
          ul{
            margin: 0 0 0 64px;
            li{
              a{
                display: inline-block;
                margin-right: 20px;
                font-size: 16px;
                line-height: 20px;
                font-weight: 500;
                color: #51267d;
                position: relative;
                &:after{
                  content: '';
                  position: absolute;
                  left: 0;
                  right: 0;
                  bottom: -7px;
                  margin: 0 auto;
                  background-color: #51267d;
                  height: 2px;
                  width: 0;
                  transition: all 0.5s linear;
                }
              }
              a:hover:after{
                width: 100%;
              }
            }
          }
        }
        .navbar-text{

          button{
            color: #fff;
            padding: 10px 25px!important;
            font-size: 16px;
            min-width: 64px;
            box-sizing: border-box;
            transition: transform .2s ease-in-out;
            font-weight: 500;
            line-height: 19px;
            border-radius: 8px;
            letter-spacing: normal;
            text-transform: none;
            background-color: #51267D;
            &:hover{
              transform: translateY(-5px);
            }
          }
          .korzina{
            position: relative;
            img{
              margin-right: 10px;
            }
            .redd{
              position: absolute;
              background-color: red;
              width: 13px;
              height: 13px;
              border-radius: 50%;
              color: #ffffff;
              display: flex;
              align-items: center;
              justify-content: center;
              font-weight: 500;
              font-size: 10px;
              left: 10px;
              top: 7px;
            }
            h6{
              cursor: pointer;
              position: relative;
              font-weight: 500;
              margin-right: 22px;
              color: #51267d;
            }
            //.cart{
            //  position: absolute;
            //  top: 55px;
            //  z-index: 999;
            //  width: 350px;
            //  background-color: #fff;
            //  box-shadow: 0 0 10px rgba(0,0,0,0.3);
            //  border-radius: 12px;
            //  padding: 25px;
            //  left: -100%;
            //  //display: none;
            //  &:before{
            //    content: "";
            //    position: absolute;
            //    width: 0;
            //    height: 0;
            //    border-bottom: 15px solid #fff;
            //    border-left: 15px solid transparent;
            //    border-right: 15px solid transparent;
            //    top: -13px;
            //   left: 30%;
            //  }
            //}
            &:hover .cart{
              //display: block;
            }
          }
        }
      }
    }
  }
  .main{
    margin: 57px 0 40px;
    background-color: #f6f6f6;
    .container{
      .category-title{
        color: #000;
        font-size: 24px;
        font-family: 'Proxima-Nova', sans-serif;
        font-weight: 500;
        line-height: 24px;
        letter-spacing: 0;
        margin-bottom: 30px;
      }
      .fix{
        .react-multi-carousel-list{
          position: static;
          ul{
            padding: 5px;
            .main-inner{
              display: inline-block;
              background: #ffffff;
              border-radius: 12px;
              padding: 10px 0;
              cursor: pointer;
              -webkit-transition: all .5s;
              -moz-transition: all .5s;
              transition: all .5s;
              white-space: nowrap;
              width: 90%;
              text-align: center;
              &:hover{
                box-shadow: 0 0 10px rgba(0,0,0,0.5);
                transform: translateY(-2px);
              }
              h6{
                font-size: 18px;
                line-height: 22px;
                font-weight: 400;
                width: 100%;
              }
            }
          }
          .react-multiple-carousel__arrow--left{
            background-color: #f6f6f6;
            border-radius: 0;
            margin-left: 5%;
            &::before{
              color:rgb(92, 33, 127)!important;
              font-weight: 900!important;
              font-size: 30px!important;
            }
          }
          .react-multiple-carousel__arrow--right{
            background-color: #f6f6f6;
            border-radius: 0;
            margin-right: 5%;
            &::before{
              color:rgb(92, 33, 127)!important;
              font-weight: 900!important;
              font-size: 30px!important;
            }
          }
        }
      }
      .type{
        color: #000;
        font-size: 28px;
        font-family: 'Proxima-Nova', sans-serif;
        font-weight: 500;
        line-height: 34px;
        letter-spacing: -0.00833em;
        margin-top: 60px;
        margin-bottom: 30px;
      }
      .row{
        .col-3{
          .card{
            box-shadow: 0 0 10px rgba(0,0,0,0.3);
            border: none;
            margin-bottom: 20px;
            border-radius: 12px;
            .card-header{
              padding: 0!important;
              border-radius: 12px;
              border-bottom: none;
              img{
                border-radius: 12px;
                cursor: pointer;
              }
            }
            .card-body{
              padding: 20px 16px;
              h6{
                color: #000;
                font-size: 20px;
                font-family: 'Proxima-Nova', sans-serif;
                font-weight: 700;
                line-height: 24px;
                letter-spacing: 0.00735em;
                height: 48px;
                cursor: pointer;
              }
              p{
                overflow: hidden;
                position: relative;
                z-index: 1;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                color: #696969;
                font-size: 16px;
                line-height: 20px;
                font-weight: 400;
                height: 40px;
                margin-top: 25px;
                cursor: pointer;
              }
              .end{
                display: flex;
                align-items: center;
                justify-content: space-between;
                .price{
                  color: #000000;
                  margin-bottom: 0!important;
                  font-style: normal;
                  font-weight: 400;
                  font-size: 18px;
                  line-height: 15px;
                }
                button{
                  font-size: 16px;
                  font-weight: 600;
                  line-height: 19px;
                  font-family: Proxima-Nova,sans-serif;
                  cursor: pointer;
                  border-radius: 8px;
                  background-color: rgba(81,38,125,.08);
                  padding: 10px 25px;
                  color: #51267d;
                }
              }
            }
          }
        }
      }
    }
  }
  .footer{
    background-color: #51267d;
    padding-top: 48px;
    .container{
      .footer-start{
        text-align: center;
       .links{
         padding-top: 32px;
         padding-right: 0px;
         padding-bottom: 48px;
         padding-left: 0px;
         a{
           margin-right: 32px;
           color: #ffffff;
           text-decoration: none;
           display: inline-block;
           font-weight: 400;
           font-size: 18px;
           line-height: 20px;
         }
       }
      }
      .footer-main{
        border-bottom: 1px solid #ffffff;
        p{
          font-size: 14px;
          line-height: 24px;
          color: #d9dbe1;
        }
        .socials{
          .social{
            margin-left: 16px;
            background-color: #623b8a;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 40px;
            height: 40px;
            i{
              //width: 32px;
              //height: 32px;
              color: #ffffff;
              font-size: 20px;
            }
          }
        }
      }
    }
  }
  .branches{
    .branches-main{
      padding-top: 20px;

    }
      .MuiPaper-root{
        cursor: pointer;
        padding: 15px;
        box-shadow: 0 0 10px rgba(0,0,0,0.3);
        border-radius: 12px;
        background-color: #fff;
        margin-top: 24px;
        margin-bottom: 24px;
        .MuiAccordionSummary-content{
          .title{
            font-size: 20px;
            font-family: 'Proxima-Nova', sans-serif;
            font-weight: 600;
            line-height: 24px;
            letter-spacing: 0.00735em;
          }
          .little-title{
            font-size: 14px;
            line-height: 30px;
            margin-top: 6px;
            font-weight: 500;
          }
        }
      }
    .MuiCollapse-root{
      .ww-50{
        flex: 0 0 50%;
        max-width: 50% ;
        //text-align: center;
        .ww-75{
         width: 100%;
        }
      }
      .title{
        font-size: 20px;
        font-family: 'Proxima-Nova', sans-serif;
        font-weight: 600;
        line-height: 24px;
        letter-spacing: 0.00735em;
      }
      .little-title{
        font-size: 14px;
        line-height: 30px;
        margin-top: 6px;
        font-weight: 500;
        margin-left: 10px;
      }
    }
  }
  .about{
    .col-12, .col-7{
      .card{
        border-radius: 15px;
        box-shadow: 0 0 10px rgba(0,0,0,0.3);
        .card-body{
          img{
            border-radius: 15px;
          }
          h6{
            margin-top: 56px;
            font-size: 20px;
            font-family: 'Proxima-Nova', sans-serif;
            font-weight: 600;
            line-height: 24px;
            letter-spacing: 0em;
            margin-bottom: 24px;
          }
          p{
            font-size: 16px;
            font-family: 'Proxima-Nova', sans-serif;
            font-weight: 300;
            letter-spacing: 0.01071em;
          }
          a{
            color: #51267d;
            font-size: 16px;
            font-family: 'Proxima-Nova', sans-serif;
            font-weight: 300;
            letter-spacing: 0.01071em;
            margin-left: 5px;
          }
          .align-items-center{
            border-bottom: 2px solid #f6f6f6;
            margin-bottom: 20px;
          }
        }
      }
    }
  }
  @media (max-width: 1200px) {
    
  }
  @media (max-width: 992px) {
    .main{
      .row{
        .col-3{
          flex: 0 0 50%;
          max-width: 50%;
        }
      }
    }
  }
  @media (max-width: 768px) {
    .about{
      .col-7{
       flex: 0 0 100%;
        max-width: 100%;
        .card{
          .card-body{
            text-align: center;
            .justify-content-between{
              display: block!important;
            }
          }
        }
      }
    }
    .branches{
      .MuiTypography-root{
        .d-flex{
          display: block!important;
          .ww-50{
            flex: 0 0 100% !important;
            max-width: 100% !important;
            text-align: center;
            .justify-content-start{

            }
            img{
              width: 100% !important;
            }
          }
        }
      }
    }
    .footer{
      .footer-start{
        .links{
          a{
            display: block!important;
            margin-bottom: 20px;
            text-align: center;
            font-size: 22px!important;
            margin-right: 0!important;
          }
        }
      }
      .footer-main{
        display: block!important;
        text-align: center;
        .socials{
          max-width: 40%;
          margin: auto;
        }
      }
      .footer-end{
        display: block!important;
        text-align: center;
      }
    }
  }
  @media (max-width: 500px) {
    .main{
      .row{
        .col-3{
          .card{
            .card-body{
              h6{
                font-size: 15px!important;
              }
              .end{
                .price{
                  display: none;
                }
                button{
                  width: 100%;
                }
              }
            }
          }
        }
      }
    }
  }
  @media (max-width: 400px) {
    .main{
      .row{
        .col-3{
          .card{
            .card-body{
              h6{
                font-size: 12px!important;
                height: 40px!important;
              }
              button{
                font-size: 10px!important;
              }
            }
          }
        }
      }
    }
  }
}
.modal{
  .modal-dialog{
    .modal-content{
      //top: 150px;
      width: 800px;
      left: -100px;
      overflow-y: scroll;
      height: 600px;
      .modal-header{
        border-bottom: none;
        .modal-title{
          display: block;
          margin-left: auto;
          .close{
            width: 47px;
            height: 47px;
            cursor: pointer;
            color: #51267d;
            background-color: #ffffff;
            border: none;
            display: block;
            margin-left: auto;
          }
        }
      }
      .modal-body{
        .ww-50{
          flex: 0 0 50%;
          max-width: 50%;
          img{
            width: 250px!important;
            height: 200px;
          }
          .plus-minus{
            width: 47px;
            height: 47px;
            font-size: 32px;
            cursor: pointer;
            color: #51267d;
            background: rgba(81,38,125,.08);
            display: flex;
            align-items: center;
            justify-content: center;
            border: none;
          }
          .value{
            width: 50px;
            height: 47px;
            margin: 0 6px;
            display: flex;
            align-items: center;
            justify-content: center;
          }
          input{
            width: 100px;
          }
          .btn-info{
            background-color: #51267d;
            font-size: 16px;
            font-weight: 500;
            line-height: 19px;
            cursor: pointer;
            border-radius: 8px;
            padding: 15px 10px;
            border: none;
            span{
              margin-right: 5px;
            }
          }
        }
        .ww-50-one{
          flex: 0 0 40%;
          max-width: 40%;
        }
        .ww-33{
            flex: 0 0 33.3%;
            max-width: 33.3%;
            .plus-minus{
              width: 47px;
              height: 47px;
              font-size: 32px;
              cursor: pointer;
              color: #51267d;
              background: rgba(81,38,125,.08);
              display: flex;
              align-items: center;
              justify-content: center;
              border: none;
            }
            .value{
              width: 50px;
              height: 47px;
              margin: 0 6px;
              display: flex;
              align-items: center;
              justify-content: center;
            }
          }
      }
    }
  }
}
@media (max-width: 992px)  {
  .modal-dialog {
    .modal-content {
      width: 100% !important;
      left: 0!important;
      .modal-body{
        .align-items-center{
          display: block!important;
          .ww-50{
            flex: 0 0 100%;
            max-width: 100%;
            text-align: center;
            .plus-minus, .value{
              width: 100%;
              margin-bottom: 20px;
              margin-top: 20px;
            }
          }

        }
        .first-zakaz{
          text-align: center;
          .ww-33{
            flex: 0 0 100%;
            max-width: 100%;
            img{
             width: 300px!important;
              height: 300px;
            }
            .plus-minus, .value{
              width: 100%;
              margin-bottom: 20px;
              margin-top: 20px;
            }
            .btn-danger{
              margin-bottom: 20px;
            }
          }
        }
      }
    }
  }
}
